import {
  Switch,
  Route
} from "react-router-dom";
import React from 'react';
import Main_view from './Main_view';
import Yygq from './yygq';
import Logic_page from './logic_page';
class APP extends React.Component{
  render(){
    return(
      <Route>
        <Switch>
        <Route exact path="/" component={Main_view}></Route>
        <Route path="/yygq" component={Yygq}></Route>
        <Route path="/logic" component={Logic_page}></Route>
        </Switch>
      </Route>
    );
  }
}

export default APP;
